'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import { createSPASassClient } from '@/lib/supabase/client'
import Link from 'next/link'
import FormControl from '../forms/form_components/FormControl'
import { DialogHeader, DialogTitle } from '../ui/dialog'
import SSOButtons from './SSOButtons'
import { Button } from '../ui/button'
import QLucideIcon from '../ui/LucideIcon'

type LoginFormProps = {
  dialog?: boolean
}

const validateEmail = (value: string): string => {
  if (!value) return 'Bitte gib eine E-Mail-Adresse ein.'
  if (!/\S+@\S+\.\S+/.test(value)) return 'Bitte gib eine gültige E-Mail-Adresse ein.'
  return ''
}

const validatePassword = (value: string): string => {
  if (!value) return 'Bitte gib dein Passwort ein.'
  if (value.length < 6) return 'Das Passwort muss mindestens 6 Zeichen lang sein.'
  return ''
}

const translateAuthError = (message: string): string => {
  const translations: Record<string, string> = {
    'Invalid login credentials': 'Ungültige Anmeldedaten. Bitte überprüfe deine E-Mail und dein Passwort.',
    'Email not confirmed': 'Deine E-Mail-Adresse wurde noch nicht bestätigt. Bitte überprüfe dein Postfach.',
    'User already registered': 'Ein Benutzer mit dieser E-Mail-Adresse ist bereits registriert.',
    'User not found': 'Benutzer nicht gefunden. Bitte überprüfe deine Eingaben.',
    'Invalid email or password': 'E-Mail oder Passwort ist falsch.'
  }
  return translations[message] || 'Ein unbekannter Fehler ist aufgetreten.'
}

export default function LoginForm({ dialog = false }: LoginFormProps) {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [errors, setErrors] = useState({ email: '', password: '', global: '' })
  const [loading, setLoading] = useState(false)
  const [showPassword, setShowPassword] = useState(false)
  const router = useRouter()

  const clearErrors = () => setErrors({ email: '', password: '', global: '' })

  const handleEmailSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    clearErrors()

    const emailError = validateEmail(email);

    if (emailError) {
      setErrors({ email: emailError, password: '', global: '' });
      return;
    }

    setLoading(true)
    try {
      const res = await fetch(`/api/auth/user-exists?email=${encodeURIComponent(email)}`)
      if (!res.ok) throw new Error(`Serverfehler: ${res.status}`)

      const { exists } = await res.json()

      if (exists) {
        setShowPassword(true);
        setLoading(false)
      } else {
        router.push(`/auth/register?email=${encodeURIComponent(email)}`);
        // Loading bleibt true während Navigation
      }
    } catch (err) {
      console.error(err)
      setErrors(prev => ({ ...prev, global: 'Fehler beim Überprüfen der E-Mail.' }))
      setLoading(false)
    }
  }

  const handlePasswordSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    clearErrors()

    const emailError = validateEmail(email)
    const passwordError = validatePassword(password)

    if (emailError || passwordError) {
      setErrors({ email: emailError, password: passwordError, global: '' })
      return
    }

    setLoading(true)
    try {
      const client = await createSPASassClient()
      const { error: signInError } = await client.loginEmail(email, password)
      if (signInError) throw signInError
      router.push('/user')
      // Loading bleibt true während Navigation
    } catch (err) {
      const message = err instanceof Error ? err.message : ''
      setErrors(prev => ({ ...prev, global: translateAuthError(message) }))
      setLoading(false)
    }
  }


  const TitleContent = (
    <div className="flex items-center gap-1">
      {showPassword && (
        <button
          type="button"
          onClick={() => setShowPassword(false)}
          className="p-0.5 text-muted-foreground hover:text-foreground"
          aria-label="Zurück"
        >
            <QLucideIcon icon='ArrowLeft' className='w-1.5 h-1.5' />
        </button>
      )}
      <span className='title'>{showPassword ? 'Anmelden' : 'Willkommen zurück!'}</span>
    </div>
  )

  return (
    <>
      {dialog ? (
        <DialogHeader>
          <DialogTitle className="title text-3xl  text-start ">
            {TitleContent}
          </DialogTitle>
        </DialogHeader>
      ) : (

        <div className="title text-3xl ext-start mt-3 mb-3 flex items-center gap-1">
          {TitleContent}</div>
      )}

      <div className="py-1 ">
        {!showPassword ? (
          <form noValidate onSubmit={handleEmailSubmit} className="space-y-1 mt-1">
            <FormControl
              type="email"
              label="E-Mail"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Deine E-Mail-Adresse"
              required
              error={errors.email}
            />

            {errors.global && (
              <div className="text-sm text-red-600">{errors.global}</div>
            )}

            <Button
              type="submit"
           disabled={loading}
              variant={loading ? "disabled" :"primary"}
              size={'full'}            >
              Weiter
            </Button>

            <p className="body-txt text-base">
              Neu bei building.earth?{' '}
              <Link href="/auth/register" className="body-txt">
                Kostenlos registrieren
              </Link>
            </p>
          </form>
        ) : (
          <form noValidate onSubmit={handlePasswordSubmit} className="space-y-1 ">
            <FormControl
              type="email"
              label="E-Mail"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Deine E-Mail-Adresse"
              required
              disabled
              error={errors.email}
            />

            <FormControl
              type="password"
              label="Passwort"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="••••••••"
              required
              error={errors.password}
            />

            <div className="mt-1 text-sm space-y-1.5">
              <Link href="/auth/forgot-password" className="hover:underline">
                Passwort vergessen?
              </Link>

            </div>

              {errors.global && (
                <div className="p-1 text-sm text-error-600 bg-error-100 rounded">
                  {errors.global}
                </div>
              )}

            <Button
              type="submit"
              disabled={loading}
              variant={loading ? "disabled" :"primary"}
              size={'full'}

            >
              Weiter

            </Button>

          </form>
        )}

        <SSOButtons from='login' onError={(error) => setErrors(prev => ({ ...prev, global: error }))} />
      </div>
    </>
  )
}